// 信息列表新增和修改接口
export function info (type, data) {
  data = qs.stringify(data)
  const obj = { method: type, url: '/info', data }
  return service(obj)
}

// 信息列表新增申请提交之后需要重新查询渲染出来
export function getInfo () {
  return service({
    method: 'get',
    url: '/info'
  })
}

